import * as React from "react";
import { useEffect } from "react";
import { Step } from "generated/graphql";
import { useStateValue } from "context/FlowContext";
import { ActionType } from "ts/enums/ActionType";
import Flow from "./Flow";
import { getGlobalConditions } from "../../helpers/globalConditions";

const FlowContainer = ({ firstStep }: { firstStep: Step}): React.ReactElement<any> | null => {
    const [, dispatch] = useStateValue();

    useEffect(() => {
        if (firstStep) {
            dispatch({ type: ActionType.setCurrentStep, step: firstStep});
            dispatch({ type: ActionType.addGlobalConditions, conditions: getGlobalConditions() });
        }
    }, [firstStep, dispatch]);


    return (
        <React.Fragment>
            <Flow firstStep={firstStep}/>
        </React.Fragment>
    );
};
export default FlowContainer;
