import {  EventHandler, InputHandlerMap, EventHandlerWithConditions } from "../ts/types";
import { EventHandlerEvent } from "../ts/enums/EventHandlerEvent";
import { ActionType } from "../ts/enums/ActionType";
import { Maybe, OutputCondition } from "../generated/graphql";
import { BuildingBlockType } from "../ts/enums/BuildingBlockType";
import { Dispatch, Reducer, ReducerAction } from "react";

export const createEventHandler = ({
    __typename,
    dispatch
}: {
    __typename: BuildingBlockType,
    dispatch: Dispatch<ReducerAction<Reducer<object, object>>>
}): EventHandlerWithConditions | EventHandler | null => {

    const eventHandlerMap: InputHandlerMap = {
        [EventHandlerEvent.unknown]: EventHandlerEvent.unknown,
        [BuildingBlockType.radio]: EventHandlerEvent.optionSelected,
        [BuildingBlockType.button]: EventHandlerEvent.valueSubmitted,
        [BuildingBlockType.back]: EventHandlerEvent.wantedToGoBack,
        [BuildingBlockType.textfield]: EventHandlerEvent.valueChanged,
    };

    const event = eventHandlerMap[__typename] || eventHandlerMap[EventHandlerEvent.unknown];

    switch (event) {
        case EventHandlerEvent.optionSelected:
            return (conditions: Array<Maybe<OutputCondition>>) => dispatch({ type: ActionType.addToTimeline, conditions });

        case EventHandlerEvent.valueChanged:
            return (conditions: Array<Maybe<OutputCondition>>) => dispatch({ type: ActionType.updateCurrentInputConditions, conditions });

        case EventHandlerEvent.valueSubmitted:
            return (conditions: Array<Maybe<OutputCondition>>) => dispatch({ type: ActionType.addToTimeline, conditions });

        case EventHandlerEvent.wantedToGoBack:
            return () => dispatch({ type: ActionType.removeLastFromTimeline });
    }

    return null;
};
