import gql from 'graphql-tag';

export const QUERY_GET_NEXT_STEP = gql`
    query GetStep($step_id: ID!, $conditions: [InputCondition!]!){
        GetNextStep(step_id: $step_id, conditions: $conditions){
            step_id
            render_type
            message
            progress
            title
            redirect_url
            buildingblocks {
                __typename
                ... on BuildingBlockTitle {title}
                ... on BuildingBlockParagraph {type markdown html}
                ... on BuildingBlockMessage {markdown html}
                ... on BuildingBlockSubmit {input_key label output_conditions {input_key, input_value, input_type}}
                ... on BuildingBlockTextfield {input_key sticky type placeholder output_conditions {input_key, input_value, input_type}}
                ... on BuildingBlockRadio {input_key type label output_conditions {input_key, input_value, input_type}}
                ... on BuildingBlockButton {input_key label type output_conditions {input_key, input_value, input_type}}
                ... on BuildingBlockQuestion {text}
                ... on BuildingBlockProgress {percentage}
                ... on BuildingBlockImage {alt_text title_text images { conversion url full_url mime_type } responsive_images { input_key url }}
            }
        }}
`;
