import * as React from 'react';

// Style
import styles from './RadioButton.module.scss';
import { RadioButtonProps } from "../../ts/types";
import { RadioButtonType } from "../../ts/enums/RadioButtonType";
import { sleep } from "../../helpers/blocks";

const RadioButton = ({
    input_key,
    type,
    label,
    output_conditions,
    eventHandler
}: RadioButtonProps): JSX.Element => {
    const onChangeHandler = () => {
        switch(type) {
        case RadioButtonType.button:
            sleep().then(() => eventHandler(output_conditions));
            break;
        case RadioButtonType.list:
            eventHandler(output_conditions);
            break;
        }
    };

    return (
        <div className={styles[type]}>
            <input className={styles.input}
                   id={input_key}
                   type="radio"
                   name={input_key}
                   value={input_key}
                   onChange={onChangeHandler}
            />
            <label className={styles.label} htmlFor={input_key}>{label}</label>
        </div>
    );
};

export default RadioButton;
