import * as React from 'react';
import { BuildingBlockSubmit, useSubmitFlowMutation } from 'generated/graphql';
import { sleep } from 'helpers/blocks';
import { useAllConditions } from "helpers/hooks";
import { useStateValue } from 'context/FlowContext';
import { ActionType } from "ts/enums/ActionType";
import { getResultPageType } from 'helpers/resultPage';

// Style
import styles from './SubmitButton.module.scss';

const SubmitButton = ({ label }: BuildingBlockSubmit): React.ReactElement<any> => {
    const [, dispatch] = useStateValue();
    const allConditions = useAllConditions();
    const [submitFlow] = useSubmitFlowMutation();

    const onClickHandler = (event: React.MouseEvent) => {
        sleep().then(() => {
            submitFlow({
                variables: {
                    data: allConditions,
                }
            }).then(response => {
                dispatch({
                    type: ActionType.getResultPage,
                    resultType: getResultPageType(allConditions)
                });
            });
        })
    };

    return (
        <button className={styles.button} type="submit" onClick={onClickHandler}>{label}
            <svg viewBox="0 0 9.95 16.91">
                <path
                    d="M10,8.5,1.75.34A1,1,0,0,0,.34.35a1,1,0,0,0,0,1.41L7.16,8.5h0L.34,15.25a1,1,0,0,0,1.41,1.42L10,8.5Z"
                    transform="translate(-0.05 -0.05)"/>
            </svg>
        </button>
    );
};

export default SubmitButton;
