import React from 'react';
import 'styles/normalizer.scss';
import 'App.scss';
import { useGetFlowQuery } from 'generated/graphql';
import { StateProvider } from 'context/FlowContext';
import FlowContainer from 'components/Flow';
import Loader from 'components/loader/Loader';
import { useGlobalConditions } from "helpers/hooks";

const App = () => {
    const globalConditions = useGlobalConditions();
    const { data, loading } = useGetFlowQuery({
        variables: {
            conditions: globalConditions,
        },
    });

    if (loading) {
        return <Loader />;
    }

    // TODO: Error handling.
    if (!data) {
        return null;
    }

    return (
        <StateProvider>
            <div className="App">
                <FlowContainer firstStep={data.GetFlow}/>
            </div>
        </StateProvider>
    );
};

export default App;
