import React, { createContext, useContext, useReducer } from 'react';
import { ActionType } from 'ts/enums/ActionType';
import { Action, FlowContextState } from 'ts/types';
import { getLatestEntryFromTimeline } from "../helpers/steps";
import { TransistionState } from "../ts/enums/TransistionState";
import { collectionWithoutTypename } from "../helpers/data";

const initialState: FlowContextState = {
    currentStep: null,
    backStep: null,
    timeline: [],
    stepTransitionState: TransistionState.exiting,
    globalConditions: [],
    currentInputConditions: [],
    resultPageType: null,
};

const flowReducer = (state: FlowContextState, action: Action) => {
    switch (action.type) {
        case ActionType.updateCurrentInputConditions:
            return {
                ...state,
                currentInputConditions: action.conditions
            };
        case ActionType.addGlobalConditions: {
            if (!action.conditions) {
                return state;
            }
            return {
                ...state,
                globalConditions: [...state.globalConditions, ...action.conditions]
            }
        }
        case ActionType.setCurrentStep: {
            return {
                ...state,
                currentStep: action.step,
            };
        }
        // This action is used for going forward in flow.
        case ActionType.addToTimeline: {
            return {
                ...state,
                timeline: [...state.timeline, {
                    step: state.currentStep,
                    // TODO: Fix error:
                    // @ts-ignore
                    conditions: collectionWithoutTypename(action.conditions),
                }],
                backStep: null,
            };
        }
        // This action is used for going back in flow.
        case ActionType.removeLastFromTimeline: {
            let timeline = [...state.timeline];
            let lastEntry = getLatestEntryFromTimeline(timeline);

            // If we have entries in the timeline get rid of the latest entry.
            if (timeline.length > 0) {
                timeline = timeline.slice(0, -1);
            }
            // If the timeline is not empty get the last entry that we will use as the current step.
            if (timeline.length) {
                lastEntry = getLatestEntryFromTimeline(timeline);
            }

            return {
                ...state,
                timeline,
                ...(lastEntry ? { currentStep: lastEntry.step } : {}),
                // This is used for keeping the step we went back from, because we cannot fetch it from timeline.
                // Only steps with values are stored there.
                backStep: state.currentStep,
            }
        }
        case ActionType.getResultPage: {
            return {
                ...state,
                resultPageType: action.resultType,
            }
        }
        default: {
            // TODO: Create error handler.
            throw new Error(`Unhandled action type: ${action.type}`);
        }
    }
};

export const StateContext = createContext(initialState);

export const StateProvider = ({ children }: { children: any }) => (
    // TODO: Fix:
    // @ts-ignore
    <StateContext.Provider value={useReducer(flowReducer, initialState)}>
        {children}
    </StateContext.Provider>
);
export const useStateValue = () => useContext<any>(StateContext);
