import * as React from 'react';
import classNames from 'classnames';

// Styles
import Styles from './TextField.module.scss';
import { TextfieldInputTypeMap, TextfieldProps } from "../../ts/types";
import { ConditionType } from "../../ts/enums/ConditionType";
import { TextfieldType } from "../../ts/enums/TextfieldType";
import { InputAttributeType } from "../../ts/enums/InputAttributeType";

const TextField = ({
    input_key,
    sticky,
    type,
    placeholder,
    eventHandler
}: TextfieldProps): JSX.Element => {
    const onChangeHandler = (event: React.ChangeEvent<HTMLInputElement>) => {
        eventHandler([{ input_key, input_value: event.target.value, input_type: ConditionType.form }]);
    };

    const classes = classNames( Styles.textfield, { [Styles.textfieldNotSticky]: sticky });

    const textfieldInputTypeMap: TextfieldInputTypeMap = {
        [TextfieldType.phonenumber]: InputAttributeType.tel,
        [TextfieldType.zipcode]: InputAttributeType.number,
        [TextfieldType.unknown]: type,
    };
    const inputType = textfieldInputTypeMap[type] || textfieldInputTypeMap[TextfieldType.unknown];

    return (
        <div className={classes}>
            <input
                type={inputType}
                placeholder={placeholder}
                id={input_key}
                name={input_key}
                onChange={onChangeHandler}/>
        </div>
    );
};

export default TextField;
