import * as React from 'react';
import { BuildingBlockImage } from 'generated/graphql';
import { MimeType } from 'ts/enums/MimeType';
import { ConversionType } from 'ts/enums/ConversionType';
import { KNOWN_IMAGE_FORMATS } from 'conf';

// Styles
import Styles from './Image.module.scss';

const Image = ({images, title_text, alt_text}: BuildingBlockImage): React.ReactElement<any> => {

    let mimeType = MimeType.unknown;
    let defaultImage = '';

    // TODO: switch any type
    const srcSet = images.reduce((acc: string, image: any) => {
        mimeType = image.mime_type;

        if(image.conversion === ConversionType.onex) {
            defaultImage = image.full_url;
        }

        if(KNOWN_IMAGE_FORMATS.includes(image.conversion)) {
            acc = `${acc} ${image.full_url} ${image.conversion},`;
        }

        if(mimeType === MimeType.svg && image.conversion === ConversionType.original) {
            acc = image.full_url;
        }

        return acc;
    }, '');

    switch (mimeType) {
        // TODO: find out why it's not comparable
        //@ts-ignore
        case MimeType.svg:
            return (
                <div className={Styles.imageBlock}>
                    <object className={Styles.image} type="image/svg+xml" aria-label={alt_text} data={srcSet}></object>
                </div>
            );
        default:
            return (
                <div className={Styles.imageBlock}>
                    <picture className={Styles.image}>
                        <source srcSet={srcSet}/>
                        <img src={defaultImage} alt={alt_text} title={title_text} />
                    </picture>
                </div>
            );
    }
}

export default Image;
