import * as React from 'react';
import { BuildingBlockMessage } from 'generated/graphql';
import ReactMarkdown from 'react-markdown';
import { useStateValue } from "../../context/FlowContext";

// Style
import Styles from './Message.module.scss';

const Message = ({markdown}: BuildingBlockMessage): React.ReactElement<any> => {
    const [{ currentInputConditions }] = useStateValue();

    // TODO: Refatoring find reliable way to get zipcode
    const zipcode = currentInputConditions.reduce((acc: string, inputCondition: any) => {
        acc = inputCondition.input_value;
        return acc;
    }, '');

    return (
        <React.Fragment>
            {zipcode && (
                <div className={Styles.zipcode}>
                    {zipcode}
                </div>
            )}
            <div className={Styles.message}>
                <ReactMarkdown source={markdown} />
            </div>
        </React.Fragment>
    );
};

export default Message;
