import * as React from 'react';
import gsap from 'gsap';

// Style
import Styles from './Loader.module.scss';

const Loader = (): React.ReactElement<any> => {
    const ref = React.createRef<HTMLDivElement>();

    React.useLayoutEffect(() => {
        if(ref.current){
            gsap.fromTo(ref.current, {opacity:0}, {opacity:1, duration: 0.3, delay: 0.6});
        }
    }, [ref])

    return (
        <div className={Styles.loader} ref={ref}>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100">
                <path fill="#DCDDDE" d="M50,25.56a24.44,24.44,0,0,0-1.54,48.83A24,24,0,0,0,66.63,67.8,24.46,24.46,0,0,0,50,25.56"/>
                <path fill="#DCDDDE" d="M67.44,68.88a25.36,25.36,0,0,1-17.56,7q-.83,0-1.65-.06a25.76,25.76,0,1,1,19.21-7ZM82.92,24A24.32,24.32,0,0,1,64.37,10.41a15.85,15.85,0,0,0-28.7,0A24.3,24.3,0,0,1,17.12,24,16,16,0,0,0,8.24,51.45,25.09,25.09,0,0,1,15.33,73.7C13,87,26.81,97.14,38.56,90.76a24,24,0,0,1,22.92,0C73.23,97.14,87.08,87,84.72,73.7c-1.11-6.2-.63-14.94,7-22.13C101.41,42.35,96.15,25.87,82.92,24Z"/>
            </svg>
        </div>
    )
};

export default Loader;
