import { Conditions } from "ts/types";
import { ResultType } from "ts/enums/ResultType";
import { InputCondition } from "../generated/graphql";

export const getResultPageType = (conditions: Conditions): ResultType => {
    const resolveMap = {
        [ResultType.home]: isHomeResultPage,
        [ResultType.components]: isComponentsResultPage,
        [ResultType.competition]: isCompetitionResultPage,
        [ResultType.business]: isBusinessResultPage
    };
    // console.log(isBusinessResultPage(conditions));

    // TODO: find out why it fails
    // @ts-ignore
    return Object.keys(resolveMap).reduce((acc: string, type: string) => {
        // TODO: find out why it fails
        // @ts-ignore
        if (resolveMap[type](conditions)) {
            acc = type;
        }
        return acc;
    }, ResultType.unknown);
};

const isHomeResultPage = (conditions: Conditions): boolean => {
    // TODO: change when we have the right conditions
    return false;
};
const isComponentsResultPage = (conditions: Conditions): boolean => {
    // TODO: change when we have the right conditions
    return false;
};
const isCompetitionResultPage = (conditions: Conditions): boolean => {
    // TODO: change when we have the right conditions
    return false;
};
const isBusinessResultPage = (conditions: Conditions): boolean => {
    // TODO: Fix types
    // @ts-ignore
    return conditions.reduce((acc: any, condition: InputCondition) => {
        // TODO: get the types fixed in the schema it sucks to write '16'.
        if (condition.input_key === '16' && condition.input_type === 'form') {
            acc = true;
        }
        return acc;
    }, false);
};
