import * as React from 'react';
import { useEffect } from "react";
import Step from './Step';
import { useStateValue } from 'context/FlowContext';
import { getLatestEntryFromTimeline, getFakeTimelineEntry } from "helpers/steps";
import { ActionType } from "ts/enums/ActionType";
import { useGetStepQuery } from 'generated/graphql';
import Loader from 'components/loader/Loader';

const StepContainer = ():any  => {
    const [{ timeline, currentStep }, dispatch] = useStateValue();
    const latestTimelineEntry = getLatestEntryFromTimeline(timeline) || getFakeTimelineEntry();

    const { data, loading } = useGetStepQuery({
        variables: {
            step_id: String(latestTimelineEntry.step.step_id),
            conditions: latestTimelineEntry.conditions,
        },
        errorPolicy: 'all'
    });

    useEffect(() => {
        if (!data) {
            return;
        }
        const loadedStep = data.GetNextStep;
        if (loadedStep !== currentStep) {
            dispatch({ type: ActionType.setCurrentStep, step: loadedStep });
        }

    }, [timeline, data, currentStep, dispatch]);

    if (!latestTimelineEntry) {
        return null;
    }

    const loadedStep = data && data.GetNextStep;

    return (
        <React.Fragment>
            {(!data || loading) && (<Loader/>)}
            {!loading && loadedStep && (
                <Step
                    key={Number(loadedStep.step_id)}
                    id={Number(loadedStep.step_id)}
                >
                    {loadedStep.buildingblocks}
                </Step>
            )}
        </React.Fragment>
    );
};
export default StepContainer;
