import { parse, ParsedUrlQuery } from "querystring";
import { KNOWN_COOKIES, KNOWN_QUERY_PARAMETERS } from "conf";
import { Conditions, Cookie } from "../ts/types";
import { createContextCondition } from "./conditions";
import { KnownQueryParameter } from "../ts/enums/KnownQueryParameter";
import { ConditionContext } from "../ts/enums/ConditionContext";

const getCurrentQueryParameters = (): ParsedUrlQuery => {
    const queryString = window.location.search;
    return parse(queryString.substring(1));
};

const getKnownQueryParams = (): ParsedUrlQuery => {
    const queryParams = getCurrentQueryParameters();
    const knownParams = knownQueryParameters();
    Object.keys(queryParams).map(property => {
        if (!knownParams.includes(property)) {
            delete queryParams[property];
        }

        return null;
    });

    return queryParams;
};

const knownQueryParameters = (): String[] => {
    return KNOWN_QUERY_PARAMETERS;
};

const transformQueryParamsIntoConditions = (queryParams: ParsedUrlQuery): Conditions => (
    Object.keys(queryParams).reduce((acc: Conditions, key: string) => {
        const conditions: Conditions = [];
        // Split utm_campaign into conditions if it match a certain pattern.
        if (key === KnownQueryParameter.utm_campaign) {
            const campaignConditions = splitCampaignParameterIntoConditions(String(queryParams[key]));
            if (campaignConditions.length) {
                return [...acc, ...campaignConditions];
            }
        }

        // Otherwise create context conditions from the other query parameters.
        conditions.push(createContextCondition(key, String(queryParams[key])));
        return [...acc, ...conditions];
    }, [])
);

const splitCampaignParameterIntoConditions = (value: string): Conditions => {
    const conditions = [];
    const splitCampaign = String(value).match(/^(.*)fcon(.*)$/);
    if (splitCampaign && splitCampaign.length === 3 ) {
        const [, campaign, fcon] = splitCampaign;
        conditions.push(createContextCondition(ConditionContext.campaign, campaign));
        conditions.push(createContextCondition(ConditionContext.fcon, fcon));

        return conditions;
    }

    return [];
};

const transformReferrerIntoConditions = (referrer: string): Conditions => {
    return [createContextCondition(ConditionContext.referrer, referrer)];
};

export const parseCookies = (cookieString: string): Cookie[] => {
    const cookiesRaw = cookieString.replace(/\s/g, '').split(';');
    return cookiesRaw.reduce((acc: Cookie[], rawCookie: string) => {
        const [key, value] = rawCookie.split('=');
        return [...acc, { key, value }];
    }, []);
};

export const getCookieConditions = (cookieString: string): Conditions => {
    const cookies = parseCookies(cookieString);
    return cookies.reduce((acc: Conditions, cookie: Cookie) => {
        if (KNOWN_COOKIES.includes(cookie.key)) {
            acc = [...acc, createContextCondition(cookie.key, cookie.value)];
        }

        return acc;
    }, []);
};

export const getGlobalConditions = (): Conditions => [
    ...(document.cookie ? getCookieConditions(document.cookie) : []),
    ...(document.referrer ? transformReferrerIntoConditions(document.referrer) : []),
    ...transformQueryParamsIntoConditions(getKnownQueryParams()),
];

