import { Conditions, Timeline, TimelineEntry } from "../ts/types";
import { ConditionType } from "../ts/enums/ConditionType";
import { InputCondition, Scalars } from "../generated/graphql";

export const getConditionsFromTimeline = (timeline: Timeline, conditionTypes: Array<ConditionType | string> = []): Conditions => {
    return timeline.reduce((acc: Conditions, entry: TimelineEntry) => {
        if (conditionTypes) {
            acc = [...acc, ...filterConditionsByType(entry.conditions, conditionTypes)];
        } else {
            acc = [...acc, ...entry.conditions];
        }

        return acc;
    }, []);
};


export const filterConditionsByType = (
    conditions: Conditions, conditionTypes: Array<ConditionType | string>
) => conditions.filter(
    condition => condition && condition.input_type && conditionTypes.includes(condition.input_type)
);

const createCondition = (key: Scalars['String'], value: Scalars['String'], type: Scalars['String']): InputCondition => ({
    input_key: key,
    input_value: value,
    input_type: type,
});

export const createContextCondition = (key: Scalars['String'], value: Scalars['String']): InputCondition => {
    return createCondition(key, value, ConditionType.context);
};
