import * as React from 'react';
import { BuildingBlockProgress } from 'generated/graphql';

// Style
import Style from './Progress.module.scss';

const Progress = ({percentage}: BuildingBlockProgress): React.ReactElement<any> => {

    const progressPercentage = {
        width: `${percentage}%`
    };

    return (
        <div className={Style.Progress}>
            <div className={Style.ProgressCurrent} style={progressPercentage}></div>
        </div>
    );
}

export default Progress;
