import * as React from 'react';
import { useEffect } from 'react';
import { BlockProps } from 'ts/types';
import { BuildingBlockType } from 'ts/enums/BuildingBlockType';
import Title from './Title';
import Paragraph from './Paragraph';
import Message from './Message';
import SubmitButton from './SubmitButton/SubmitButton';
import RadioButton from "./RadioButton";
import BasicButton from './Button';
import Question from './Question';
import TextField from './TextField';
import ButtonBack from './ButtonBack';
import Progress from './Progress';
import Image from './Image';
import { createEventHandler } from "../../helpers/eventHandler";
import { useStateValue } from "../../context/FlowContext";

const Block = ({ type, data, prefetchStep }: BlockProps): React.ReactElement<any> | null => {
    const [, dispatch] = useStateValue();

    // Prefetch step for all building blocks that support it.
    useEffect(() => {
        prefetchStep({ blockType: type, ...data});
    }, [type, data, prefetchStep]);

    switch (type) {
        case BuildingBlockType.back:
            // TODO fix error.
            // @ts-ignore
            return (<ButtonBack eventHandler={createEventHandler({ ...data, dispatch })} />);
        case BuildingBlockType.progress:
            return (<Progress {...data}/>);
        case BuildingBlockType.question:
            return (<Question {...data} />);
        case BuildingBlockType.title:
            return (<Title {...data} />);
        case BuildingBlockType.paragraph:
            return (<Paragraph {...data} />);
        case BuildingBlockType.radio:
            return (<RadioButton {...data} eventHandler={createEventHandler({ ...data, dispatch })} />);
        case BuildingBlockType.message:
            return (<Message {...data} />);
        case BuildingBlockType.button:
            return (<BasicButton {...data} eventHandler={createEventHandler({ ...data, dispatch })} />);
        case BuildingBlockType.textfield:
            return (<TextField {...data} eventHandler={createEventHandler({ ...data, dispatch })}/>);
        case BuildingBlockType.submit:
            return (<SubmitButton {...data} />);
        case BuildingBlockType.image:
            return (<Image {...data} />);
        default:
            return null;
    }
};

export default Block;
