import { useStateValue } from "../context/FlowContext";
import { getConditionsFromTimeline } from "./conditions";
import { ConditionType } from "../ts/enums/ConditionType";
import { getGlobalConditions } from "./globalConditions";

export const useAllConditions = () => {

    const [{ timeline }] = useStateValue();
    const timelineConditions = getConditionsFromTimeline(timeline, [ConditionType.form]);
    const globalConditions = getGlobalConditions();

    return [...timelineConditions, ...globalConditions];
};

export const useGlobalConditions = () => getGlobalConditions();
