import * as React from 'react';
import { useCallback } from 'react';
import { useApolloClient } from "react-apollo-hooks";
import { QUERY_GET_NEXT_STEP } from "./query";
import { PrefetchParams, StepProps } from 'ts/types';
import { BuildingBlockType } from "ts/enums/BuildingBlockType";
import { ConditionType } from "ts/enums/ConditionType";
import { filterConditionsByType } from "helpers/conditions";
import { collectionWithoutTypename } from 'helpers/data';
import Block from 'components/Blocks/Block';
import gsap from "gsap";

// Styles
import Styles from './Step.module.scss';

const Step = ({ id, children }: StepProps): React.ReactElement<any> =>  {
    const client = useApolloClient();
    const ref = React.createRef<HTMLDivElement>();

    React.useLayoutEffect(() => {
        if(ref.current){
            gsap.fromTo(ref.current, {opacity:0}, {opacity:1, duration: 0.5});
        }
    }, [ref])

    const prefetchStep = useCallback(
        (
            // TODO: Define types in types document:
            { output_conditions: conditions, blockType }: PrefetchParams
        ) => {

            // TODO: Define prefetchable blocks somewhere in a conf like place.
            //  But things will also change when we get conditions back on building blocks.
            const prefetchableBuildingBlocks: Array<BuildingBlockType> = [
                BuildingBlockType.radio,
            ];

            if (!conditions || !prefetchableBuildingBlocks.includes(blockType)) {
                return;
            }

            client.query({
                query: QUERY_GET_NEXT_STEP,
                variables: {
                    // TODO: Make sure that step_id type does not vary.
                    // TODO: When API is responding with input type on building blocks the use that!
                    step_id: String(id),
                    conditions: collectionWithoutTypename(
                        filterConditionsByType(conditions, [ConditionType.form])
                    )
                }
            });
        },
        [id, client],
    );

    return (
        <React.Fragment>
            <div className={Styles.step} ref={ref}>
                {children && children.map(
                    (data:any, blockId: number) => !!data && (
                        <React.Fragment key={`${id}-${blockId}`}>
                            <Block type={data.__typename} data={data} prefetchStep={prefetchStep}/>
                    </React.Fragment>
                    )
                )}
            </div>
        </React.Fragment>
    );
};
export default Step;
