import * as React from "react";
import { useStateValue } from "context/FlowContext";
import Step from "components/Step/Step";
import StepContainer from "components/Step";
import Result from "components/Result/Result";

const Flow = ({ firstStep }: { firstStep: any }) => {
    const [{ timeline, resultPageType }] = useStateValue();
    return (
        <React.Fragment>
            {!resultPageType && timeline.length === 0 && firstStep && (
                <Step id={Number(firstStep.step_id)}>
                    {firstStep.buildingblocks}
                </Step>
            )}
            {!resultPageType && timeline.length >= 1 && (
                <StepContainer />
            )}
            {resultPageType && (
                <Result type={resultPageType} />
            )}
        </React.Fragment>
    );
};

export default Flow;
