import * as React from 'react';
import { sleep } from 'helpers/blocks';
import { BasicButtonProps } from 'ts/types';
import { useStateValue } from "../../context/FlowContext";

// Style
import styles from './Button.module.scss';

const BasicButton = ({ label, type, eventHandler, output_conditions }: BasicButtonProps): React.ReactElement<any> => {
    const [{ currentInputConditions }] = useStateValue();

    const onClickHandler = () => sleep().then(() => {
        const conditions = [
            // If a value has been entered by another building block eg. a text field then add that.
            ...(currentInputConditions.length ? currentInputConditions : []),
            // If the building block has output conditions on its own use those.
            ...(output_conditions.length ? output_conditions : []),
        ];

        if (conditions.length) {
            return eventHandler(conditions);
        }
    });

    return (
        <button className={styles[type]} type="button" onClick={onClickHandler}>{label}</button>
    );
};

export default BasicButton;
