import * as React from 'react';
import { ResultType } from 'ts/enums/ResultType';
import ResultHome from './ResultHome';
import ResultComponents from './ResultComponents';

// TODO: set other type then any
const Result = ({ type }: any): React.ReactElement<any> | null => {

    switch (type) {
        case ResultType.home:
            return (<ResultHome />);
        case ResultType.components:
            return (<ResultComponents />);
        // TODO: Change this when result page is created:
        case ResultType.business:
            alert(' I am business result page');
            return (<ResultHome />);
        // TODO: Change this when result page is created:
        case ResultType.competition:
            alert(' I am competition result page');
            return (<ResultHome />);
        case ResultType.unknown:
        default:
            return (<ResultHome />);
    }
};

export default Result;
